import React from 'react';
import {connect} from 'react-redux';
import {MD_INPUT_UPDATED} from './constants';
import './style.css';

const Markdown = ({state, onInputUpdate}) => {

    const inputUpdated = (e) => {
        onInputUpdate(e.target.value)
    };

    return (
        <div className="md">
            <textarea className="md__input" name="md" onChange={inputUpdated}>
                {state}
            </textarea>
        </div>
    );

};

export default connect(
    state => ({
        state: state.mdInput
    }),
    dispatch => ({
        onInputUpdate: (text) => {
            dispatch({type: MD_INPUT_UPDATED, payload: text});
        }
    })
)(Markdown);



