import React from 'react';
import Markdown from '../Markdown/';
import Preview from '../Preview/';
import FaAngleDoubleDown from 'react-icons/lib/fa/angle-double-down';
import FaAngleDoubleUp from 'react-icons/lib/fa/angle-double-up';
import smoothScroll from '../../services/smoothScrolling';
import './style.css';


const WorkArea = () => {

    const scrollToPreview = (e) => {
        // TODO: btnH иногда прицепляется от SVG иконки
        const btnH = e.target.scrollHeight;
        const targetY = document.querySelector('.main__preview_container').scrollHeight + btnH;
        smoothScroll(targetY, 360)
    };

    const scrollToInput = () => {
        smoothScroll(0, 360)
    };
    

    return (
        <section className="main">
            <div className="main__md_container">
                <Markdown/>
            </div>
            <div className="main__btn_scroll main__btn_scroll_bottom" onClick={scrollToPreview}>
                Tap to see html <FaAngleDoubleDown/>
            </div>
            <div className="main__preview_container">
                <Preview />
            </div>
            <div className="main__btn_scroll main__btn_scroll_top" onClick={scrollToInput}>
                Tap to return to markdown <FaAngleDoubleUp/>
            </div>
        </section>
    );

}

export default WorkArea;



