import {MD_INPUT_UPDATED} from './constants';

const initialState = `
## This is markdown-editor

- Type some Markdown on the left
- See HTML in the right
- Profit
`;

export default function mdInput(state = initialState, action) {

    switch (action.type) {
        case MD_INPUT_UPDATED:
            return action.payload;

        default:
            return state;
    }
}


