import React, {Component} from 'react';
import './style.css';

class Header extends Component {
    render() {
        return (
            <header>
                <div className="header__logo">
                    <img className="header__logo_img" src="/img/markdown.png" alt="logo" height="100%"/>
                    <span className="header__logo_text">Markdown editor</span>
                </div>
            </header>
        );
    }
}

export default Header;



